.scrolled {
  transition: opacity 1s ease-in-out;
}
.scrolled__block {
  position: relative;
  overflow: hidden;
}
.scrolled__content {
  display: flex;
  width: 100%;
  gap: 25px;
  justify-content: center;
}
.scrolled__item {
  width: 256px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  position: relative;
  z-index: 10;
}
.scrolled__item.active {
  opacity: 1;
}
.scrolled__item img {
  object-fit: contain;
}
.scrolled__line {
  position: relative;
  z-index: 5;
  display: flex;
  flex-direction: row;
  gap: 30px;
}
.scrolled__line-content {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.scrolled__line-content.scroll-right {
  animation: scroll-right 120s infinite linear;
}
.scrolled__line-content.scroll-left {
  animation: scroll-left 120s infinite linear;
}

@keyframes scroll-left {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes scroll-right {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}